import React from 'react';
import './Contact.css';
import contact from './A8qQTCQ.png';
import { connect } from 'react-redux'
import Slide from 'react-reveal/Slide';

const Contact = React.memo(
    (props)=>{
        const { museum, lang } = props.app;
        let phonenumber = [], url = [], email = [];
        if(museum!=undefined) {
            if (museum.phonenumber != undefined)
                phonenumber = museum.phonenumber.split(', ');
            if (museum.url != undefined)
                url = museum.url.split(', ');
            if (museum.email != undefined)
                email = museum.email.split(', ');
            return (
                <div className='contact'>
                    <Slide left>
                    <div className='contact-text'>
                        <div className='textlinebig'>{lang===undefined||lang==='ru'? ' ' : lang==='kg'? ' ' : 'Our contacts'}</div><br/>
                        {lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Adress'}:<br/>
                        {museum.adress}<br/><br/>
                        {lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Phone'}:<br/>
                        {
                            (phonenumber).map((element) =>
                                <div>{element}</div>
                            )
                        }
                        <br/>
                        Web:<br/>
                        {
                            (url).map((element) =>
                                <a className='link-white' href={'http://'+element}>{element}</a>
                            )
                        }
                        <br/><br/>
                        E-mail:<br/>
                        {
                            (email).map((element) =>
                                <div>{element}</div>
                            )
                        }
                        <br/>
                    </div>
                    </Slide>
                    <Slide right>
                        <img className='contact-map' src={contact}/>
                    </Slide>
                </div>
            )
        }
        else return null
    }
)

function mapStateToProps (state) {
    return {
        app: state.app
    }
}

export default connect(mapStateToProps)(Contact);
