import React from 'react';
import './About.css';
import { month } from '../../redux/constants/other'
import { connect } from 'react-redux'

const about =  React.memo(
    (props) =>{
        const { lang } = props.app;
        const {type, element} = props;
        let date;
        if(type==''){
            let dateStart = element.dateStart.split('T')[0].split('-')
            let timeStart = element.dateStart.split('T')[1].split(':')
            let dateEnd = element.dateEnd.split('T')[0].split('-')
            date = ''
            if(dateStart[0]!=dateEnd[0])
                date = dateStart[2]+' '+month[dateStart[1]]+' '+dateStart[0]+', '+(parseInt(timeStart[0])+6)+':'+timeStart[1]+' - '+dateEnd[2]+' '+month[dateEnd[1]]+' '+dateEnd[0]
            else
                date = dateStart[2]+' '+month[dateStart[1]]+', '+(parseInt(timeStart[0])+6)+':'+timeStart[1]+' - '+dateEnd[2]+' '+month[dateEnd[1]]+' '+dateEnd[0]
        }
        if(element.description!=undefined&&!Array.isArray(element.description)){
            element.description = element.description.split('|');
        }
        if(element.description_ru!=undefined&&!Array.isArray(element.description_ru)){
            element.description_ru = element.description_ru.split('|');
            element.description_kg = element.description_kg.split('|');
            element.description_eng = element.description_eng.split('|');
        }
            return (
                <div className='about-museum'>
                    <div className='about-second'>
                        <div className='about-second-text'>
                            <div className='textlinebig about-name' style={{display: 'inline-block'}}>
                                {lang===undefined||lang==='ru'? element.name_ru : lang==='kg'? element.name_kg : element.name_eng }
                                {element.name===undefined? null : element.name }
                            </div>
                        </div>
                        <br/>
                        {type==''?
                            <>
                            <div className='about-second-text'>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Genre'}:</div> {lang===undefined||lang==='ru'? element.type_ru : lang==='kg'? element.type_kg : element.type_eng} <br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Date'}:</div> {date}<br/>
                                {(lang===undefined||lang==='ru'? element.description_ru[0] : lang==='kg'? element.description_kg[0] : element.description_eng[0])!==''?
                                    <>
                                    <br/>
                                    <div>
                                        {(lang===undefined||lang==='ru'? element.description_ru : lang==='kg'? element.description_kg : element.description_eng).map((element) => {
                                            return <>{element}<br/><br/></>
                                        })}
                                    </div>
                                    </>
                                    :
                                    null
                                }
                            </div>
                            </>:null}
                        {type==''?
                            <>
                            <div className='about-second-text'>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Author'}:</div> {element.author!=undefined?element.author.name:null}<br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? ' ' : lang==='kg'? ' ' : 'Years of life'}:</div> {element.author!=undefined?element.author.yearsOfLife:null}<br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Year'}:</div> {element.date} <br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Material'}:</div> {lang===undefined||lang==='ru'? element.author.styleOrMaterial_eng : lang==='kg'? element.author.styleOrMaterial_kg : element.author.styleOrMaterial_eng} <br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Genre'}:</div> {element.genre!=undefined?(lang===undefined||lang==='ru'? element.genre.name_ru : lang==='kg'? element.genre.name_kg : element.genre.name_eng):null} <br/>
                                <div className='about-property-name'>{lang===undefined||lang==='ru'? '' : lang==='kg'? '' : 'Views'}:</div> {element.views==undefined?'0':element.views} <br/>
                                {(lang===undefined||lang==='ru'? element.description_ru[0] : lang==='kg'? element.description_kg[0] : element.description_eng[0])!==''?
                                    <>
                                    <br/>
                                    <div>
                                        {(lang===undefined||lang==='ru'? element.description_ru : lang==='kg'? element.description_kg : element.description_eng).map((element) => {
                                            return <>{element}<br/><br/></>
                                        })}
                                    </div>
                                    </>
                                    :
                                    null
                                }
                            </div>
                            </>:null
                        }
                        {type==''?
                            <>
                            <div className='about-second-text'>
                                <div className='about-property-name'>:</div> {element.styleOrMaterial}<br/>
                                <div className='about-property-name'>:</div> {element.date} <br/>
                                <div className='about-property-name'>:</div> {element.author} <br/>
                                <div className='about-property-name'>:</div> {element.price}<br/>
                                {element.description[0]!==''?
                                    <>
                                    <br/>
                                    <div>
                                        {(element.description).map((element) => {
                                            return <>{element}<br/><br/></>
                                        })}
                                    </div>
                                    </>
                                    :
                                    null
                                }
                            </div>
                            </>:null
                        }
                    </div>
                </div>
            );
        }
)

function mapStateToProps (state) {
    return {
        app: state.app
    }
}

export default connect(mapStateToProps)(about);
